import UniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import UniBackground from '@/components/uni-background/uni-background.vue';
import share from '@/components/share';
export default {
	components: {
		share,
		UniNavBar,
		UniBackground,
	},
	data() {
		return {
			scroll:0,
			loaded: false,
			currentEpd: 1,
			data: {
				guessList: [{},{},{},{}] 
			},
			shareList: [],
			navButtons:{
				back:{
					type:'circle',
					text:'取消'
				},
				share:{
					type:'circle'
				},
				love:{
					type:'circle',
					
				},
				cart:{
					type:'circle',
					url:"../cart1/cart1"
				},
				// textbtn:{
				// 	text:'取消'
				// }
			}
		};
	},
	onPageScroll(val){
		this.scroll = val.scrollTop;
	},
	async onLoad(){
		let detailData = await this.$api.json('detailData');
		let shareList = await this.$api.json('shareList');
		this.loaded = true;
		this.data = detailData;
		this.shareList = shareList;
		uni.setNavigationBarTitle({
			title: detailData.title
		})
	},
	methods:{
		imageOnLoad(key,index){
			this.$set(this.data[key][index], 'loaded', 'loaded');
		},
		changeEpd(index){
			let list = this.data.episodeList;
			let epd = list[index];
			this.$api.msg(`切换到第${epd}项`);
			this.currentEpd = epd;
		},
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		//收藏
		favorite(){
			this.data.favorite = !this.data.favorite;
		}
	},
	//处理遮罩层物理返回键
	onBackPress(){
		if(this.$refs.share.show){
			this.$refs.share.toggleMask();
			return true;
		}
	}
}
